// http://webapi.amap.com/maps?v=1.3&key=d94035ac264f0cc5b293199360ca0e1e
import amap from '../../js_sdk/amap-wx.js';
import {
	uniPopup
} from '@dcloudio/uni-ui'


import getLoaction from '../../components/getLoaction.js'


export default {
	data() {
		return {
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			ticketCity: {
				cityName: '',
				cityCode: ''
			}, // 地理信息
			ticketDate:{ }, // 日期信息
			searchValue: '', // 搜索信息
			amapPlugin: null,
			key: 'a2b2f62638c5291714db573e08ce7046', //高德key
		};
	},
	onLoad(){
		this.amapPlugin = new amap.AMapWX({  
			key: this.key  
		});  
	},
	onUnload(){
		uni.hideLoading()
	},
	components: {
		uniPopup
	},
	onShow(){
		console.log('我执行了')
		this.ticketDate = this.$store.state.ticketDate
		this.ticketCity = this.$store.state.ticketCity
		console.log('wowo', this.ticketCity)
	},
	methods:{
		// 返回上级目录
		goBack() {
			 uni.navigateBack({
			 	
			 })
		},
		gotoChoseDate(str){
			uni.navigateTo({
				url:"../chosedate/chosedate?type=" + str
			})
		},
		gotoOrder(){
			uni.navigateTo({
				url: '../viewpointlist/ticketorder'
			})
		},
		search(){
			uni.navigateTo({
				url: '../viewpointlist/viewpointlist?cityName=' + this.ticketCity.cityName + '&cityCode=' + this.ticketCity.cityCode
			})
		},
		gotoChoseCity(str){
			uni.navigateTo({
				url: '../choseCity/choseCity?type=' + str
			})
		},
		// 获取当前位置
		getLocation1(){
			let _this = this
		    getLoaction(function(res){
				console.log(res)
				if(res.targetData.city){
					_this.ticketCity.cityName = res.targetData.city
					return
				}
				_this.ticketCity.cityName = res.targetData.regeocodeData.addressComponent.city
				
			})
			
		},
		// 跳转意见反馈
		goFeedback(){
			uni.navigateTo({
				url:'../mine/feedback'
			})
		}
	}
}
